export default class NewsApi {
  constructor(query, apiKey) {
    this.query = query;
    this.apiKey = apiKey;
  }
  getNews() {
    return fetch('https://praktikum.tk/news/v2/everything?q=путешествия&apiKey=b2d8c2d2189d416894d3c9c77ce6d1b7')
      .then(res => {
        if (res.ok) {
          return res.json();
        }
        return Promise.reject(`Ошибка: ${res.status}`);
      })
      .catch(() => Promise.reject(new Error("Не могу получить данные карточек")));
  }
}
