import "./css/main.css";

import NewsApi from './js/modules/NewsApi.js';
import { API_KEY, query } from './js/constants/constants.js';

const api = new NewsApi;
api.getNews()
  .then((data) => {
    data.forEach(item => {
      const card = new NewsCard(item);
      NewsCardList.add(card.create());
    })
  })
  .catch(error => {
    console.log(error)
  })

